#title {
  text-align: center;
  font-family: "Trebuchet MS", sans-serif;
  padding-top: 2px;
}

#secondTitle {
  text-align: center;
  font-family: arial, sans-serif;
  font-size: 17px;
}

#table {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
}

#table td,
#table th {
  border: 1px solid #ddd;
  padding: 8px;
  font-size: 15px;
}

#table tr {
  background-color: #fdb927;
}

#table tr:hover {
  background-color: #552583;
  color: #fdb927;
}

#table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: #552583;
  color: #fdb927;
  font-size: 20px;
}
